.container .card {
    width: 330px;
    height: 450px;
    border-radius: 8px;
    box-shadow: 0 2px 2px rgba(0,0,0,2);
    overflow: hidden;
    margin: 20px;
    text-align: center;
    transition: all 0.25s;
}
.container .card:hover{
    transform: translateY(-15px);
    box-shadow: 0 12px 16px rgba(0,0,0,2);
}
.container .card img{
    width: 330px;
    height: 220px;
}
.container .card .card-body h5{
    font-weight: 600;
}

.container .card .card-body p{
    font-size: 16px;
    font-weight: 600;
}
.container .card .card-body a{
    font-weight: 500;
    text-decoration: none;
    color: #3498db
}
